import React from "react";
import styled from "styled-components";
import { Card, Button, Typography, message } from "antd";

const { Title, Paragraph } = Typography;

const Container = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.45);
  z-index: 9999;
`;

const StyledCard = styled(Card)`
  width: 420px;
  border-radius: 8px;
  text-align: center;
`;

type Props = {
  onClose?: () => void;
};

const SSOPage: React.FC<Props> = ({ onClose }) => {
  const handleMockSignIn = () => {
    const token = `mock-token-${Date.now()}`;
    try {
      localStorage.setItem("sso_token", token);
    } catch (e) {}
    message.success(" : mock SSO");
    setTimeout(() => onClose && onClose(), 600);
  };

  const handleExternalRedirect = (provider: string) => {
    const url = `https://example.com/oauth2/authorize?provider=${encodeURIComponent(provider)}`;
    window.open(url, "_blank");
  };

  return (
    <Container>
      <StyledCard>
        <Title level={3}>SSO </Title>
        <Paragraph> SSO   .</Paragraph>

        <div style={{ display: "flex", gap: 12, justifyContent: "center", marginTop: 12 }}>
          <Button type="primary" onClick={() => handleExternalRedirect("google")}>Google </Button>
          <Button onClick={() => handleExternalRedirect("github")}>GitHub </Button>
        </div>

        <div style={{ marginTop: 16 }}>
          <Button type="dashed" onClick={handleMockSignIn}>Mock SSO </Button>
        </div>

        <div style={{ marginTop: 18 }}>
          <Button onClick={() => onClose && onClose()}></Button>
        </div>
      </StyledCard>
    </Container>
  );
};

export default SSOPage;
